<template>
    <div class="wrapper">
        <img class="wrapper__img"
            src="http://rhb782ep7.hn-bkt.clouddn.com/%E7%BC%96%E7%BB%84.png?e=1662370220&token=6gAXP7vKAVfOY_fEu7FKGWQii8w9bsTQkmKWkfh-:r0tLZ8HpYVP89goAAmW8gQyZeKo=">
        <div class="wrapper__input">
            <input class="wrapper__input__content" placeholder="用户名" v-model="username">
        </div>
        <div class="wrapper__input">
            <input class="wrapper__input__content" placeholder="请输入密码" type="password" v-model="password"
                autocomplete="new-password">
        </div>
        <div class="wrapper__login-button" @click="handleLogin">
            登陆
        </div>
        <div class="wrapper__login-link" @click="handleRegisterClick">
            立即注册
        </div>
        <Toast v-if="show" :message="message" />
    </div>
</template>
<script>
import { reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'
import { post } from '../../utils/request'
import Toast, { useToastEffect } from '../../components/Toast'

const useLoginEffect = (showToast) => {
    const router = useRouter()
    const data = reactive({
        username: '',
        password: ''
    })
    const handleLogin = async () => {
        try {
            const { username, password } = data
            if (username == null || username.length === 0) {
                showToast('用户名不能为空')
                return
            }
            if (password == null || password.length === 0) {
                showToast('密码不能为空')
                return
            }
            // 假设点击登录直接就登录了
            const result = await post('/api/user/login', {
                username: username,
                password: password
            })
            if (result?.errno === 0) {
                localStorage.isLogin = true
                router.push({ name: 'home' })
            } else {
                showToast('登录失败')
            }
        } catch (e) {
            showToast('请求失败')
        }
    }
    const { username, password } = toRefs(data)
    return { username, password, handleLogin }
}

const useRegisterEffect = () => {
    const router = useRouter()
    const handleRegisterClick = () => {
        router.push({ name: 'Register' })
    }
    return { handleRegisterClick }
}

export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: 'Login',
    components: { Toast },
    setup() {
        const { show, message, showToast } = useToastEffect()
        const { username, password, handleLogin } = useLoginEffect(showToast)
        const { handleRegisterClick } = useRegisterEffect()
        return { handleLogin, handleRegisterClick, username, password, show, message }
    }
}
</script>
